}

.story figure {
    width: 105%;
    height: 105%;
    /* change min-height OR change the resolution of the images aka no wide images */
    margin: 0;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 1s ease;
    background-position: bottom;
}

